

main {    width: 70%;
height: 750px;
background-color: rgb(0, 0,) 0;
color: antiquewhite;
position: relative;
text-align: center;
margin: auto;
}
form{
width: 70%;
height: 600px;
position: absolute;
top: 50%;
left: 50%;
background-color: rgb(0, 0, 0);
transform: translate(-50%,-50%);
text-align: center;
border-radius: 20px;
border: 3px solid rgb(0, 195, 255);

}

input{width: 60%;
height: 50px;
border-radius: 25px;
background-color: rgb(0, 0, 0);
margin: 15px;
border: 3px solid rgb(0, 195, 255);
text-align: center;
color: rgb(0, 195, 255);
font-size: 20px;
outline: 0;
}

input:focus{border: 4px solid rgb(0, 195, 255);
outline: 0;
}
h1{width: 100%;
height: 50px;
font-size: 30px;
}
label{
font-size: 30px;
background-color: rgb(0, 0, 0);

}

.title{font-size: 60px;
background-color: rgb(0, 0, 0);
margin-bottom: 30px;
border-radius: 20px;
margin-top: 30px;
}
button{
width: 23%;
height: 40px;
font-size: 25px;
color: rgb(253, 255, 255);
border: 3px solid rgb(0, 195, 255);
border-radius: 25px;
margin-top: 20px;
}
button:hover{background-color: white;
color: rgb(0, 195, 255);
transition: background-color 2s;
transition: color 1s;
}
article{background-color: transparent;
margin-top: 130px;
}

@media (max-width:950px)
{main{width: 85%;}
form{width: 90%;}
input{width: 80%;}
button{width: 40%;}
h1{font-size: 30px;}
.title{font-size: 40px;}
article{margin-top: 80px;}
}
@media (max-width:800px)
{
main{width: 90%;}
    form{width: 98%;}
    h1 h2{font-size: 25px;}
.title{font-size: 50px;}
article{margin-top: 80px;}

}
@media (max-width:650px)
{
    article{margin-top: 50px;}

    main{width: 99%;}
    form{width: 90%;}
    input{width: 80%;}
    button{width: 50%;}
    h1{font-size: 20px;}
.title{font-size: 40px;
margin-top: 70px;
}

}


